.navbar {
    height: 5rem;
    width: 100vw;
    margin: 0 auto;
    padding: 1rem- 2rem;
    display: flex;
    align-items: center;
    justify-content:space-between ;
    position: sticky;
    top: 0;
    z-index: 3;
    max-width: 75rem;
    background: rgb(30, 30, 30);
}

.logo {
    object-fit: cover;
    height: 4rem;
    width: 4rem;
    
}

.desktopMenulistItem {
    margin: 1rem;
    cursor: pointer;
}
.desktopMenulistItem:hover {
    color: yellow;
    padding-bottom: 0.5rem ;
    border-bottom: 3px solid yellow;
}


.desktopMenuBtn {
    background: white;
    color: black;
    display: flex;
    border: none;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    cursor: pointer;
}

.desktopMenuImg {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
    cursor: pointer;
}

.active {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px  solid yellow;
}

.mobMenu {
    display: none;
    object-fit: cover;
    height: 1.8rem;
}

.navMenu {
    position: absolute;
    top:4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

.listItem {
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(40, 40, 40);

}
@media screen and (max-width:720px){
    .mobMenu {
        display: flex;
    }

    .desktopMenu {
        display: none;
    }
    .desktopMenuBtn {
        display: none;
    }
    
}